.work {
    margin-top: 15rem;
}
.work .work-header {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    padding-left: 5rem;
}
.work .work-header span {
    display: flex;
}
.work .work-header span h2 {
    margin: 0 0.5rem;
    padding: 0;
    font-family: "Cyborg";
    font-weight: 900;
}
.work .projects {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 3rem 0 7rem 0;
    padding: 0rem 5rem;
}
.work .projects .single {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: 6rem 0rem;
}
@media (min-width: 760px) and (max-width: 1024px) {
    .work .projects .single {
        flex-direction: column;
        margin: 5rem 0rem;
    }
}
@media (min-width: 480px) and (max-width: 759px) {
    .work .projects .single {
        flex-direction: column;
        margin: 3rem 0rem;
    }
}
@media (max-width: 479px) {
    .work .projects .single {
        flex-direction: column;
        margin: 2rem 0rem;
    }
}
.work .projects .single.reverse {
    flex-direction: row-reverse;
}
@media (min-width: 760px) and (max-width: 1024px) {
    .work .projects .single.reverse {
        flex-direction: column;
    }
}
@media (min-width: 480px) and (max-width: 759px) {
    .work .projects .single.reverse {
        flex-direction: column;
    }
}
@media (max-width: 479px) {
    .work .projects .single.reverse {
        flex-direction: column;
    }
}
.work .projects .single.reverse .single-img img {
    transform: perspective(800px) rotateY(-25deg) scale(0.8) rotateX(10deg);
    filter: blur(0px);
    opacity: 1;
    transition: 0.6s ease all;
}
.work .projects .single.reverse .single-img img:hover {
    box-shadow: 0px 19px 67px -38px rgba(155, 156, 159, 0.8);
    transform: perspective(800px) rotateY(15deg) translateY(50px) rotateX(10deg)
        scale(1);
    filter: blur(0.2px);
    opacity: 1;
}
.work .projects .single .single-img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60%;
}
@media (min-width: 760px) and (max-width: 1024px) {
    .work .projects .single .single-img {
        width: 100%;
    }
}
@media (min-width: 480px) and (max-width: 759px) {
    .work .projects .single .single-img {
        width: 100%;
    }
}
@media (max-width: 479px) {
    .work .projects .single .single-img {
        width: 100%;
    }
}
.work .projects .single .single-img img {
    width: 700px;
    height: 398px;
    border-radius: 15px;
    box-shadow: rgba(155, 156, 159, 0.42) 0px 60px 123px -25px,
        rgba(155, 156, 159, 0.08) 0px 35px 75px -35px;
    transform: perspective(800px) rotateY(25deg) scale(0.8) rotateX(10deg);
    filter: blur(0px);
    opacity: 1;
    transition: 0.6s ease all;
}
.work .projects .single .single-img img:hover {
    box-shadow: 0px 19px 67px -38px rgba(155, 156, 159, 0.8);
    transform: perspective(800px) rotateY(-15deg) translateY(-50px)
        rotateX(10deg) scale(1);
    filter: blur(0.2px);
    opacity: 1;
}
@media (min-width: 480px) and (max-width: 759px) {
    .work .projects .single .single-img img {
        width: 500px;
        height: 284px;
    }
}
@media (max-width: 479px) {
    .work .projects .single .single-img img {
        width: 350px;
        height: 199px;
    }
}
.work .projects .single .single-detail {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 40%;
    color: #fff;
    padding: 1rem;
}
@media (min-width: 760px) and (max-width: 1024px) {
    .work .projects .single .single-detail {
        width: 100%;
    }
}
@media (min-width: 480px) and (max-width: 759px) {
    .work .projects .single .single-detail {
        width: 100%;
    }
}
@media (max-width: 479px) {
    .work .projects .single .single-detail {
        width: 100%;
    }
}
.work .projects .single .single-detail h4 {
    border: 3px solid #e84393;
    color: #e84393;
    box-shadow: 6px 6px 0px 0px #e84393;
    text-transform: uppercase;
    font-weight: 700;
    padding: 0.7rem 1.1rem;
    font-family: "Cyborg";
    font-weight: 900;
    margin-bottom: 3rem;
}
@media (min-width: 480px) and (max-width: 759px) {
    .work .projects .single .single-detail h4 {
        font-size: 1.8rem;
    }
}
@media (max-width: 479px) {
    .work .projects .single .single-detail h4 {
        font-size: 1.5rem;
    }
}
.work .projects .single .single-detail p {
    margin: 0;
    font-size: 18px;
    text-align: center;
    margin-bottom: 3.5rem;
}
@media (min-width: 480px) and (max-width: 759px) {
    .work .projects .single .single-detail p {
        font-size: 17px;
    }
}
@media (max-width: 479px) {
    .work .projects .single .single-detail p {
        font-size: 16px;
    }
}
.work .projects .single .single-detail p span {
    color: #e84393;
}
.work .projects .single .single-detail .button-wrap {
    height: max-content;
    display: flex;
    align-items: center;
    justify-content: center;
}
.work .projects .single .single-detail .button-wrap button {
    padding: 0.5rem 0.9rem;
    text-transform: lowercase;
    font-family: "Cyborg";
    font-weight: 900;
    font-size: 1.1rem;
    background: #e84393;
    background-color: #e84393;
    background-image: linear-gradient(315deg, #e84393 0%, #fd79a8 100%);
    border: none;
    border-radius: 7px;
    box-shadow: 9px 7px 32px -11px rgba(20, 233, 86, 0.74);
    transition: all 0.3s ease-in-out 0s;
    cursor: pointer;
    outline: none;
    position: relative;
}
.work .projects .single .single-detail .button-wrap button::before {
    content: "";
    border-radius: 7px;
    min-width: calc(100px + 12px);
    min-height: calc(43px + 12px);
    border: 3px solid #fd79a8;
    box-shadow: 0 0 60px rgba(20, 233, 86, 0.64);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: all 0.3s ease-in-out 0s;
}
.work .projects .single .single-detail .button-wrap button:hover,
.work .projects .single .single-detail .button-wrap button:focus {
    transform: translateY(-6px);
}
.work .projects .single .single-detail .button-wrap button:hover::before,
.work .projects .single .single-detail .button-wrap button:focus::before {
    opacity: 1;
}
.work .projects .single .single-detail .button-wrap button::after {
    content: "";
    width: 30px;
    height: 30px;
    border-radius: 7px;
    border: 6px solid #e84393;
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: ring 1.5s infinite;
}
.work .projects .single .single-detail .button-wrap button:hover::after,
.work .projects .single .single-detail .button-wrap button:focus::after {
    animation: none;
    display: none;
}
@keyframes ring {
    0% {
        width: 30px;
        height: 30px;
        opacity: 1;
    }
    100% {
        width: 150px;
        height: 60px;
        opacity: 0;
    }
}
